
.settings-container .sidebar.hidden {
  -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
          transform: translateX(100%);
}

.settings-container .sidebar .actions .done-btn {
  position: fixed;
  bottom: 14px;
  -webkit-margin-start: 250px;
     -moz-margin-start: 250px;
          margin-inline-start: 250px;

}

.settings-container .sidebar .done-btn:hover {
  -webkit-box-shadow: 0 0 0 3px rgba(121, 119, 119, 0.397);
          box-shadow: 0 0 0 3px rgba(121, 119, 119, 0.397);
