.settings-container .sidebar {
  top: 0px;
  right: 0px;
  position: fixed;
  height: 100%;
  background: white;
  border-left: 1px solid #D7D7DB;
  -webkit-box-shadow: 0 1px 6px 0 rgba(13, 13, 14, 0.1);
          box-shadow: 0 1px 6px 0 rgba(13, 13, 14, 0.1);

  width: 400px;

  -webkit-transition: 0.3s cubic-bezier(0, 0, 0, 1);

  -o-transition: 0.3s cubic-bezier(0, 0, 0, 1);

  transition: 0.3s cubic-bezier(0, 0, 0, 1);
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  -o-transition-property: transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;

  z-index: 9000;
}

.settings-container .sidebar .settings-wrapper {
  margin: 40px;
}

.settings-container .toggle-icon {
  -webkit-user-select: none;
