  position: fixed;
  top: 15px;
  right: 30px;
  cursor: pointer;
  padding: 5px;
  z-index: 10000;
}

.settings-container .toggle-icon:hover {
  background: rgba(215, 215, 219, 0.432);
}

.settings-wrapper section {
  padding-bottom: 5px;
}

