.file-selector .drag-area {
  margin: auto;

  width: 50%;
  height: 200px;

  background: white;
  -webkit-box-shadow: 0 1px 6px 0 rgba(13, 13, 14, 0.1);
          box-shadow: 0 1px 6px 0 rgba(13, 13, 14, 0.1);

  text-align: center;
  padding: 70px;
  cursor: pointer;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
}
.file-selector .drag-area p {
  padding-top: 10px;
  color: rgba(0, 0, 0, 0.8);
}
.file-selector p.msg-danger {
  color: rgb(245, 78, 78);
}
.file-selector input[type=file] {
  display: none;
}


